import { loadScript } from "@paypal/paypal-js";

import utils from "./utils";

class Payments {
    static init(container)
    {
        let buttons = $('#paypal-buttons', container);
        if (buttons.length > 0) {
            setTimeout(() => {
                loadScript({
                    clientId: container.find('[name=paypal_client_id]').val(),
                    currency: 'EUR',
                    locale: 'it_IT',
                }).then((paypal) => {
                    paypal.Buttons({
                        createOrder() {
                            return fetch("/payment/do", {
                                method: "POST",
                                body: {
                                    type: 'paypal',
                                    amount: $('#paypalCredit [name=amount]').val(),
                                    description: $('#paypalCredit [name=description]').val(),
                                },
                            }).then((response) => response.json()).then((order) => order.id);
                        },
                        onApprove(data) {
                            return fetch("/payment/status/paypal", {
                                method: "POST",
                                body: {
                                    orderID: data.orderID
                                },
                            });
                        }
                    }).render('#paypal-buttons');
                });
            }, 200);
        }
    }
}

export default Payments;
